<template>
  <div>
    <el-button type="success"
               style="margin-bottom: 20px;"
               @click="getMapDataList">
      刷新
    </el-button>
    <el-input
        v-model="pageParams.keyword"
        placeholder="模糊搜索"
        @input="getMapDataList"
        clearable prefix-icon="Search"
        style="margin-left: 20px; width: 400px; margin-bottom: 20px;"
    />
    <el-button
        type="primary"
        @click="addMapData = true"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      新增IP数据
    </el-button>
    <el-button
        type="primary"
        @click="updateOciCfgIpData"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      同步已有的oci配置最新数据
    </el-button>
    <el-button
        type="danger"
        @click="batchRemove"
        :disabled="!batchSelected.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量删除
    </el-button>
    <!-- 可滑动的表格 -->
    <div style="overflow-x: auto;">
      <el-table
          :data="mapDataPageRsp"
          style="width: 100%; min-width: 800px;"
          v-loading="loading"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="35"/>
        <el-table-column label="IP地址" width="260">
          <template #default="{ row }">
            <el-tooltip :content="row.ip" placement="top">
              <el-tag type="success" @click="copyToClipboard(row.ip)">
                {{ row.ip }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="国家">
          <template #default="{ row }">
            <el-tooltip :content="row.country" placement="top">
              <el-tag type="primary">
                {{ row.country }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="地区">
          <template #default="{ row }">
            <el-tooltip :content="row.area" placement="top">
              {{ row.area }}
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="城市">
          <template #default="{ row }">
            <el-tooltip :content="row.city" placement="top">
              {{ row.city }}
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="组织">
          <template #default="{ row }">
            <el-tooltip :content="row.org" placement="top">
              {{ row.org }}
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="ASN">
          <template #default="{ row }">
            <el-tooltip :content="row.asn" placement="top">
              <el-tag type="warning">
                {{ row.asn }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="纬度">
          <template #default="{ row }">
            <el-tooltip :content="row.lat" placement="top">
              <el-tag type="primary">
                {{ row.lat }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="经度">
          <template #default="{ row }">
            <el-tooltip :content="row.lng" placement="top">
              <el-tag type="primary">
                {{ row.lng }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
<!--        <el-table-column label="创建时间">-->
<!--          <template #default="{ row }">-->
<!--            <el-tooltip :content="row.createTime" placement="top">-->
<!--              <el-tag type="info">-->
<!--                {{ row.createTime }}-->
<!--              </el-tag>-->
<!--            </el-tooltip>-->
<!--          </template>-->
<!--        </el-table-column>-->

        <el-table-column label="操作" width="220">
          <template #default="{ row }">
            <el-button @click="updateIpInfo(row)" type="success" link>
              更新IP信息
            </el-button>
            <el-button @click="handleRemove(row)" type="danger" link>
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="pageParams.currentPage"
        v-model:page-size="pageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>

  <el-dialog
      v-model="addMapData"
      title="新增地图IP数据"
      width="40%"
      @close="addMapData = false"
  >
    <el-form label-width="200px">
      <el-form-item label="IP地址（支持v4、v6）" label-width="150px">
        <el-input
            class="cf-input"
            v-model="addIpDataParams.ip"
            placeholder="请输入IP地址"
            clearable
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="addMapData = false">取消</el-button>
        <el-button type="primary" @click="addIpData()" :loading="addIpDataLoading">新增</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import {confirmDialog, copyToClipboard} from "@/js/common";
import {ElMessage} from "element-plus";

export default {
  components: {},
  mounted() {
    this.getMapDataList()
  },
  data() {
    return {
      addMapData: false,
      addIpDataLoading: false,
      removeIpDataLoading: false,
      updateIpDataLoading: false,
      loading: true,
      pageParams: {
        keyword: '',
        currentPage: 1,
        pageSize: 10,
      },
      addIpDataParams: {
        ip: ''
      },
      mapDataPageRsp: [
        {
          id: '1',
          ip: '1.1.1.1',
          country: 'AU',
          area: 'New South Wales',
          city: 'Sydney',
          org: 'CLOUDFLARENET',
          asn: 'AS13335',
          lat: -33.859336,
          lng: 151.203624,
          createTime: '2025-07-30 15:08:47'
        }
      ],
      total: 0,
      batchSelected: [],
      removeIds: [],
    };
  },
  methods: {
    copyToClipboard,
    async getMapDataList() {
      this.loading = true
      await this.$axios.post('/ipData/page', {...this.pageParams})
          .then(response => {
            if (response.data.success) {
              this.mapDataPageRsp = response.data.data.records
              this.total = response.data.data.total
              ElMessage({
                message: '获取IP数据列表成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.cleanCache()
          })
    },
    async addIpData() {
      this.addIpDataLoading = true
      await this.$axios.post('/ipData/add', {...this.addIpDataParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '新增IP数据成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.cleanCache()
            this.getMapDataList();
          })
    },
    async updateOciCfgIpData() {
      const confirmed = await confirmDialog('确定要同步所有已存在的oci配置的最新IP数据吗？', '同步已有的oci配置的IP信息');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.loading = true
      this.$axios.post('/ipData/loadOciIpData', {})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '同步IP数据任务下发成功，请稍后自行刷新列表查看',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.cleanCache()
            this.getMapDataList();
          })
    },
    async batchRemove() {
      const confirmed = await confirmDialog('确定要删除IP数据吗？', '删除首页地图IP数据');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.removeIpDataLoading = true
      this.removeIds = this.batchSelected.map(record => record.id);

      await this.$axios.post('/ipData/remove', {idList: this.removeIds})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '删除IP数据成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.cleanCache()
            this.getMapDataList();
          })
    },
    async updateIpInfo(row) {
      const confirmed = await confirmDialog('确定要更新IP：' + row.ip + ' 的数据信息吗？', '更新IP数据');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.updateIpDataLoading = true
      await this.$axios.post('/ipData/update', {id: row.id})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '更新IP数据成功',
                type: 'success',
                duration: 2000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
          .finally(() => {
            this.cleanCache()
            this.getMapDataList();
          })
    },
    cleanCache() {
      this.batchSelected = []
      this.removeIds = []
      this.loading = false
      this.addIpDataLoading = false
      this.removeIpDataLoading = false
      this.updateIpDataLoading = false
      this.addMapData = false
      this.addIpDataParams.ip = ""
    },
    handleRemove(row) {
      this.batchSelected.push(row)
      this.batchRemove()
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.getMapDataList();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.getMapDataList();
    },
    handleSelectionChange(val) {
      this.batchSelected = val;
    },
  }
}
</script>

